/* ==== VARIABLES ==== */
:root {
  --bg1:#ffecd2; --bg2:#fcb69f;
  --primary:#ff7043; --primary-2:#ff9800;
  --accent:#43a047; --muted:#9e9e9e;
  --card:#ffffff; --text:#333; --sub:#555;
  --ring: rgba(255,112,67,.25);
  --radius:16px;
}

body.dark, .wrap.dark {
  --bg1:#1a1a1a; --bg2:#2a2a2a;
  --card:#2e2e2e; --text:#f5f5f5;
  --sub:#bbb; --primary:#ff5722;
  --primary-2:#ff9800; --accent:#4caf50;
  --muted:#757575; --ring: rgba(255,152,0,.35);
}

body { margin: 0; }
* { box-sizing: border-box; }

/* ==== LOGIN ==== */
.wrap.login {
  display:flex;flex-direction:column;justify-content:center;align-items:center;
  height:100vh;font-family:'Poppins';
  background:linear-gradient(135deg,#ffecd2,#fcb69f)
}

.input-name {
  padding:12px 14px;border-radius:12px;border:1px solid #ccc;
  font-size:16px;outline:none;width:260px;text-align:center;
}

.btn-start {
  margin-top:14px;padding:12px 16px;border:none;border-radius:12px;
  font-weight:700;background:#ff7043;color:#fff;cursor:pointer;
}

/* ==== MAIN WRAPPER ==== */
.wrap {
  font-family:'Poppins','Segoe UI',sans-serif;
  min-height:100vh; padding:20px;
  background:linear-gradient(135deg,var(--bg1),var(--bg2));
  transition:background .3s ease;
}

.title {
  font-size:clamp(1.8rem,4vw,2.6rem);
  font-weight:800;text-align:center;margin:10px 0 22px;
  background:linear-gradient(45deg,var(--primary),var(--primary-2));
  -webkit-background-clip:text;background-clip:text;color:transparent;
}

/* ==== SEARCH BAR ==== */
.bar {
  max-width:820px;margin:0 auto 28px;padding:14px;
  background:var(--card);border-radius:var(--radius);
  box-shadow:0 6px 16px rgba(0,0,0,.12);
  display:flex;gap:10px;flex-wrap:wrap;align-items:center;
}

.input {
  flex:1 1 260px;min-width:0;padding:12px 14px;border-radius:12px;
  border:1px solid #e8e8e8;font-size:16px;outline:none;
}
.input:focus {
  border-color:var(--primary);box-shadow:0 0 0 4px var(--ring);
}

.btn {
  padding:12px 16px;border:none;border-radius:12px;
  font-weight:700;cursor:pointer;
}
.btn-primary { background:var(--primary);color:#fff }
.btn-accent { background:var(--accent);color:#fff }
.btn-muted { background:var(--muted);color:#fff }
.btn:disabled { opacity:.6;cursor:not-allowed }

/* ==== LOGOUT BUTTON ==== */
.logout-btn {
  position:fixed;top:15px;right:20px;padding:10px 14px;
  border:none;border-radius:8px;background:#ff5722;color:#fff;
  font-weight:bold;cursor:pointer;
}

/* ==== SUARA ==== */
.sound-control {
  text-align:center;margin-bottom:10px;
}

.dm { text-align:center;margin-bottom:15px; }

/* ==== GRID ==== */
.grid {
  display:grid;gap:20px;
  grid-template-columns:repeat(auto-fit,minmax(250px,1fr));
  max-width:1200px;margin:0 auto;
}

.card {
  background:var(--card);border-radius:20px;padding:20px;
  box-shadow:0 4px 12px rgba(0,0,0,.15);
  display:flex;flex-direction:column;overflow:hidden;
  transition:transform .22s ease,box-shadow .22s ease;
}
.card:hover {
  transform:translateY(-4px);
  box-shadow:0 12px 26px rgba(0,0,0,.22)
}

.emoji { font-size:2rem;text-align:center;margin-bottom:10px }
.title3 { color:var(--text);margin:0 0 10px;text-align:center }
.scroll { flex:1;overflow:auto;padding-right:6px }
.meta { color:var(--sub);font-size:14px }

/* ==== HIGHLIGHT ==== */
.hl {
  background:#ffeb3b;color:#000;font-weight:700;
  padding:0 2px;border-radius:4px;
}

/* ==== SKELETON ==== */
.skeleton .sk { border-radius:8px;background:#eee }
.skeleton .sk-1 { height:20px;width:70%;margin-bottom:10px }
.skeleton .sk-2 { height:14px;width:90%;margin-bottom:6px;background:#f3f3f3 }
.skeleton .sk-3 { height:14px;width:80%;background:#f3f3f3 }

/* ==== EMPTY ==== */
.empty {
  text-align:center;color:var(--sub);font-size:1.05rem;
  grid-column:1 / -1;
}

/* ==== PAGINATION ==== */
.pager {
  margin-top:22px;display:flex;justify-content:center;
  gap:8px;flex-wrap:wrap;
}

@media(max-width:640px){
  .card{max-height:unset}
  .speak{padding:8px 12px;font-size:13px}
}
